body {
  background: #03a9f4;
}
#wrapper {
  max-width: 800px;
  box-shadow: 0px 0px 5px #000;
  border-radius: 6px;
  background-color: #fff;
}
#footer {
  padding-top: 1.2em;
}
.padding1 {
  padding-top: 1.3em;
  padding-bottom: 1.3em;
}
.padding1em {
  padding: 1em;
}
.bg-black {
  background-color: #000;
  color: #fff;
}
.bg-black hr {
  border-top: 1px solid #2b2b2b;
}
.bg-white {
  background-color: #fff;
}
.navbar {
  margin-bottom: 0;
  border-radius: 0;
}
.img-bordered {
  box-shadow: 0px 0px 10px #000;
}
.img-centered {
  margin: auto;
}
a.navbar-brand {
  padding-top: 0;
  padding-bottom: 0;
}
.table > thead > tr > th,
.table > tfoot > tr > th {
  text-align: center;
  vertical-align: middle;
}
.table > tbody > tr > td {
  vertical-align: middle;
  text-align: center;
}
.noTextCenter.table > tbody > tr > td {
  text-align: left;
}
.strong {
  font-weight: bold;
}
.noMarginTopBottom {
  margin-top: 0;
  margin-bottom: 0;
}
.ui360,
.sm2-360ui {
  margin: auto;
}
#sidebar > h3,
#sidebar > h4 {
  text-decoration: underline;
}
.noPadding4 {
  padding: 0;
}
@media screen and (max-width: 484px) {
  .hidden-484 {
    display: none;
  }
}
#vote-btn > button:focus {
  outline: none;
}
.vote-num {
  font-size: 0.75em;
}
.mainSearchForm input,
.mainSearchForm button {
  height: 50px;
  font-weight: bold;
}
.mainSearchForm input {
  font-size: 1.5em;
}
.btn {
  text-shadow: 1px 1px 2px #000;
}
.mTop6 {
  margin-top: 10px;
  margin-bottom: 5px;
}
.full-width {
  width: 100%;
}
.box-shadow {
  box-shadow: 0px 0px 5px #000000;
  margin-bottom: 15px;
}
ul.navbar-nav,
ul.navbar-nav li a {
  font-weight: bold;
}
a:focus {
  outline: none;
}
@media screen and (min-width: 767px) {
  img.small-square {
    min-height: 104.167px;
  }
  .box-shadow {
    height: 104.167px;
    overflow: hidden;
  }
  .logo-mizik {
    display: none !important;
  }
}
.transparent {
  background-color: transparent;
}
img.logo-mizik {
  height: 50px;
}
#page-content {
  font-size: 16px;
}
.hide {
  display: none;
}
#progress,
.hide-panel {
  display: none;
}
.progress {
  height: 35px;
}
.progress-bar {
  font-size: 18px;
  font-weight: bold;
  line-height: 35px;
}
.color-black,
.black {
  color: #000;
}
.color-black a,
.black a {
  color: #000;
}
.white {
  color: #fff;
}
.white a {
  color: #fff;
}
.audio-image {
  background-size: cover;
  left: 0;
  right: 0;
  z-index: 1;
}
.audio-content {
  opacity: 1;
  filter: blur(0);
  -webkit-filter: blur(0);
}
.pagination {
  font-weight: bold;
}
@media screen and (min-width: 768px) {
  .btlr {
    border-top-left-radius: 6px;
  }
  .btrr {
    border-top-right-radius: 6px;
  }
  .bblr {
    border-bottom-left-radius: 6px;
  }
  .bbrr {
    border-bottom-right-radius: 6px;
  }
}
